import React, { useState } from "react";
import styles from "./Service.module.css";

import gallery1Img from "../../../assets/donateSlider1.jpg";
import gallery2Img from "../../../assets/donateSlider2.jpg";
import gallery3Img from "../../../assets/donateSlider3.jpg";
import { useNavigate } from "react-router-dom";


function Service() {

    let navigate = useNavigate();
    let [hover, setHover] = useState(false);
    let link = "https://docs.google.com/forms/d/e/1FAIpQLScQRE7vFFPaDJhbdBZJubFX-HUFLBmZTmewkxaIPVhz0pQXsQ/viewform?usp=send_form";
    function handleClick() {
        navigate(link)
    }



    return (<div className={styles["service"]}>
        <div className={styles["service-head"]}>"Donate to Make<br/> an Impact"</div>
        <div className={styles["service-gallery"]}>
            <img src={gallery2Img} alt='gallery2Img' />
            <div>
                <div className={styles["text"]} style={{ opacity: hover ? "1" : "0" }}>Imagine for a moment that you are struggling to make ends meet.
                    You're worried about how you'll pay for basic necessities like food, shelter, and healthcare.
                    Now, imagine that someone steps forward and offers a helping hand - a small donation that can
                    make all the difference in your life. That feeling of relief and gratitude is immeasurable. By
                    donating to a cause you care about, you have the power to make that difference in someone's life.
                    You can provide hope and support to those who need it most, and create a ripple effect of kindness
                    and generosity in the world. So, if you have the means to give, think about the impact you can make.
                    Your donation may just be the lifeline that someone desperately needs.</div>
                <img src={gallery1Img} alt='gallery1Img' />
            </div>
            <img src={gallery3Img} alt='gallery3Img' />
        </div>
        <button onClick={handleClick} onMouseEnter={() => { setHover(true) }} onMouseLeave={() => { setHover(false) }}>Donate Now</button>
    </div>)
}

export default Service;