import React from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import LoginGif from "../../assets/login.jpg";
import styles from "./Register.module.css";

function Register() {

    let navigate = useNavigate();
    let [error, setError] = useState(false);


    function handleClick(e) {
        e.preventDefault();
        let gmailLength = formData.gmail.length;

        if (formData.gmail.slice(gmailLength - 10, gmailLength) === "@gmail.com") {
            navigate("/1");
        }
        else{
            setError(true);

        }
   

    }

    function handleChange(e) {
        if (e.target.name === "gmail") {
            setFormData((preValue) => { return { gmail: e.target.value, password: preValue.password, name: preValue.name } });
        }
        if (e.target.name === "password") {
            setFormData((preValue) => { return { gmail: preValue.gmail, password: e.target.value, name: preValue.name } });
        }
        if (e.target.name === "name") {
            setFormData((preValue) => { return { gmail: preValue.gmail, password: preValue.value, name: e.target.value } });
        }
    }

    let [formData, setFormData] = useState({ gmail: "", password: "", name: "" });


    return (<div className={styles["body"]} >


        <div className={styles["box"]}>
            <img src={LoginGif} alt="LoginGif" className={styles["gif"]} />

            <div className={styles["form"]}>

                <div className={styles["formHead"]}>
                    <div>Welcome to NayePankh!</div>
                    <div>Enter your details to register</div>
                </div>

                <form className={styles["formBody"]}>
                    <input type="text" id="name" name="name" placeholder="Enter Your Name" value={formData.name} onChange={handleChange} required />
                    <input type="text" id="gmail" name="gmail" placeholder="Enter Your Gmail" value={formData.gmail} onChange={handleChange} required />
                    <input type="password" id="password" name="password" placeholder="Enter Your Password" value={formData.password} onChange={handleChange} autoComplete="on" required />
                    {error ? <div className={styles["error-handler"]}>Incorrect gmail or password</div> : ""}
                    <div className={styles["btns"]}>
                        <button onClick={handleClick}>Register</button>
                        <button onClick={() => { navigate("/login") }}>Sign In</button>
                    </div>

                </form>
            </div>
        </div>

    </div>)
}

export default Register;
