import React from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import LoginGif from "../../assets/login.jpg";
import styles from "./Login.module.css";
import axios from "axios";

function Login() {

    let navigate = useNavigate();
    let [error, setError] = useState(false);


    async function handleClick(e) {
        e.preventDefault();
        let gmailLength = formData.gmail.length;
        if (formData.gmail.slice(gmailLength - 10, gmailLength) === "@gmail.com") {
            /*
            try {
                let response = await axios.post("/auth/login", formData);
                navigate(`/${response.data._id}`);
            }
            catch (err) {
                console.error(err);
            }
            */
            navigate("/1");
        }
        else {
            setError(true);
        }
    }

    function handleChange(e) {
        if (e.target.name === "gmail") {
            setFormData((preValue) => { return { gmail: e.target.value, password: preValue.password } });
        }
        if (e.target.name === "password") {
            setFormData((preValue) => { return { gmail: preValue.gmail, password: e.target.value } });
        }
    }

    let [formData, setFormData] = useState({ gmail: "", password: "" });


    return (<div className={styles["body"]} >


        <div className={styles["box"]}>
            <img src={LoginGif} alt="LoginGif" className={styles["gif"]} />

            <div className={styles["form"]}>

                <div className={styles["formHead"]}>
                    <div>Welcome back!</div>
                    <div>Welcome back to NayePankh, Enter your details</div>
                </div>

                <form className={styles["formBody"]}>
                    <input type="text" id="gmail" name="gmail" placeholder="Enter Your Gmail" value={formData.gmail} onChange={handleChange} required />
                    <input type="password" id="password" name="password" placeholder="Enter Your Password" value={formData.password} onChange={handleChange} autoComplete="on" required />
                    {error ? <div className={styles["error-handler"]}>Incorrect gmail or password</div> : ""}

                    <div className={styles["btns"]}>
                        <button onClick={handleClick}>Sign In</button>
                        <button onClick={() => { navigate("/register") }}>Register</button>
                    </div>

                </form>
            </div>

        </div>

    </div>)
}

export default Login;
